import { Component, ReactNode } from "react";
import style from "./Login.module.css";
import RotatableSvg from "../rotatable-svg/RotatableSvg";
import { AnimationTypes } from "../rotatable-svg/AnimationTypes";
import { ReactComponent as spongeBob } from '../../svg/sponge-bob.svg';
import { Redirect } from "react-router-dom";

type Props = {
    login: (login: string, password: string) => void,
    username: string | null,
    role: string | null
}

class Login extends Component<Props> {
    state = {
        login: "",
        password: ""
    }

    loginChanged = (event: React.FormEvent<HTMLInputElement>): void => {
        this.setState({login: event.currentTarget.value});
    }

    passwordChanged = (event: React.FormEvent<HTMLInputElement>): void => {        
        this.setState({password: event.currentTarget.value});
    }

    onLogin = (): void => {
        this.props.login(this.state.login, this.state.password);
        this.setState({            
            login: "",
            password: ""
        });
    }

    render(): ReactNode {
        return (
            <div className={style.container}>
                {
                    this.props.username &&
                        <Redirect to={"/reports"} />
                }
                <RotatableSvg animationName={AnimationTypes.Swing} left={870} top={200} width={200} svg={spongeBob} />
                <div  className={style.loginForm}>
                    <input type={"text"} value={this.state.login} onChange={this.loginChanged} placeholder="Логін"></input>
                    <input type={"password"} value={this.state.password} onChange={this.passwordChanged} placeholder="Пароль"></input>
                    <button onClick={this.onLogin}>Увійти</button>
                </div>
            </div>
        );
    }
}

export default Login;