import { Component, ReactNode } from "react";
import { NavLink, Redirect } from "react-router-dom";
import style from "./Navbar.module.css";
import { HiLogout } from "react-icons/hi";

type Props = {
    logout: () => void,
    username: string | null,
    role: string | null
}

class Navbar extends Component<Props> {    
    render(): ReactNode {
        return (
            <div className={style.container}>
                {
                    !this.props.username &&
                        <Redirect to={"/"} />
                }
                <div className={style.navbar}>
                    <NavbarLink link={"/reports"} name={"Звіти"} />
                    <NavbarLink link={"/stats"} name={"Статистика"} />
                    {
                        this.props.role === "admin" &&
                            <NavbarLink link={"/admin"} name={"Адмін панель"} />
                    }
                </div>
                {
                    this.props.username &&
                        <HiLogout className={style.logoutButton} onClick={this.props.logout}/>
                }
            </div>
        );
    }
}

type NavLinkProps = {
    link: string,
    name: string
}

class NavbarLink extends Component<NavLinkProps> {
    render(): ReactNode {
        return (
            <div className={style.navlink} >
                <NavLink to={this.props.link}>{this.props.name}</NavLink>
            </div>
        );
    }
}

export default Navbar;