import style from "./Report.module.css";
import { Component, ReactNode } from "react";
import { ReportItem } from "./ReportItem";
import ValuePicker, { ValuePickerTypes } from "../value-picker/ValuePicker";
import { HiPencil, HiCheck, HiX } from "react-icons/hi";
import { getDayOfWeek, getLocaleDateString, getTodaysTimestamp, isDateInSameDay } from "../../utils/time-utils";
import { calculateMoney } from "../../utils/report-utils";

type Props = {
    item: ReportItem,
    adminMode: boolean,
    onEdit: (id: string, report: ReportItem) => void
}

class Report extends Component<Props> {
    state = {
        distance: this.props.item.distance,
        sports: this.props.item.sports,
        learning: this.props.item.learning,
        cooking: this.props.item.cooking,
        wakeUp: this.props.item.wakeUp,
        goToSleep: this.props.item.goToSleep,
        money: this.props.item.money,
        arePenaltiesApply: this.props.item.arePenaltiesApply,
        isEditMode: false
    }

    onDistanceActivityChange = (value: number) => this.setState({distance: value});
    onSportsActivityChange = (value: number) => this.setState({sports: value});
    onLearningActivityChange = (value: number) => this.setState({learning: value});
    onCookingActivityChange = (value: number) => this.setState({cooking: value});
    onWakeUpTimeChange = (value: number) => this.setState({wakeUp: value});
    onGoToSleepTimeChange = (value: number) => this.setState({goToSleep: value});
    
    getCalculatedMoney = (): number => {
        const report: ReportItem = {
            id: this.props.item.id,
            date: this.props.item.date,
            arePenaltiesApply: this.state.arePenaltiesApply,
            distance: this.state.distance,
            sports: this.state.sports,
            learning: this.state.learning,
            cooking: this.state.cooking,
            wakeUp: this.state.wakeUp,
            goToSleep: this.state.goToSleep,
            money: this.state.money
        }        
        return calculateMoney(report);
    }

    getMoneyString = (money: number): string => {
        const sign = money > 0 ? "+" : "";
        return `${sign}${money} ₴`;
    }

    onCancel = (): void => {
        this.setState({
            distance: this.props.item.distance,
            sports: this.props.item.sports,
            learning: this.props.item.learning,
            cooking: this.props.item.cooking,
            wakeUp: this.props.item.wakeUp,
            goToSleep: this.props.item.goToSleep,
            arePenaltiesApply: this.props.item.arePenaltiesApply
        });
        this.toggleEditMode();
    }

    toggleEditMode = (): void => {
        this.setState({
            isEditMode: !this.state.isEditMode
        });
    }

    togglePenaltiesApply = (): void => {
        if (this.props.adminMode && this.state.isEditMode) {
            this.setState({arePenaltiesApply: !this.state.arePenaltiesApply});
        }
    }

    submitChanges = (): void => {
        const updatedReport: ReportItem = {
            id: this.props.item.id,
            distance: this.state.distance,
            sports: this.state.sports,
            learning: this.state.learning,
            cooking: this.state.cooking,
            wakeUp: this.state.wakeUp,
            goToSleep: this.state.goToSleep,
            arePenaltiesApply: this.state.arePenaltiesApply,
            money: this.getCalculatedMoney(),
            date: this.props.item.date
        };

        this.props.onEdit(this.props.item.id, updatedReport);
        this.toggleEditMode();
    }

    render(): ReactNode {
        const isEditable = this.props.adminMode || isDateInSameDay(this.props.item.date);
        const isEditing = isEditable && this.state.isEditMode;

        return (
            <div className={style.item}>
                <div className={style.money}>
                    <span>{this.getMoneyString(this.getCalculatedMoney())}</span>
                </div>
                {
                    isEditable && !this.state.isEditMode &&
                        <div className={style.controlPanel}>
                            <HiPencil className={style.button} onClick={this.toggleEditMode} />
                        </div>
                }
                {
                    this.state.isEditMode &&
                        <div className={style.controlPanel}>
                            <HiX className={style.button} onClick={this.onCancel} />
                            <HiCheck className={style.button} onClick={this.submitChanges} />
                        </div>
                }
                <div className={style.borderTop}></div>
                <div className={style.content}>
                    <div className={style.date}>{getLocaleDateString(this.props.item.date)}</div>
                    <div className={style.timeTrack}>
                        <div className={style.activityTrack}> 
                            <ValuePicker isEditable={isEditing} value={this.state.wakeUp} step={30} unit={""} type={ValuePickerTypes.Date} onChange={this.onWakeUpTimeChange} />
                            <span>Підйом</span>
                        </div>
                        <span>-</span>
                        <div className={style.activityTrack}>
                            <span>Відбій</span>
                            <ValuePicker isEditable={isEditing} value={this.state.goToSleep} step={30} unit={""} type={ValuePickerTypes.Date} onChange={this.onGoToSleepTimeChange} />
                        </div>
                    </div>
                    <div className={style.activityTrack}>
                        <div className={style.activity}>
                            <span className={style.name}>Дистанція</span>
                            <ValuePicker isEditable={isEditing} value={this.state.distance} step={0.5} unit={"км."} type={ValuePickerTypes.Number} onChange={this.onDistanceActivityChange} />
                        </div>
                        <div className={style.activity}>
                            <span className={style.name}>Спорт</span>
                            <ValuePicker isEditable={isEditing} value={this.state.sports} step={1} unit={"год."} type={ValuePickerTypes.Number} onChange={this.onSportsActivityChange} />
                        </div>
                        <div className={style.activity}>
                            <span className={style.name}>Навчання</span>
                            <ValuePicker isEditable={isEditing} value={this.state.learning} step={1} unit={"год."} type={ValuePickerTypes.Number} onChange={this.onLearningActivityChange} />
                        </div>
                        <div className={style.activity}>
                            <span className={style.name}>Кулінарія</span>
                            <ValuePicker isEditable={isEditing} value={this.state.cooking} step={1} unit={"раз."} type={ValuePickerTypes.Number} onChange={this.onCookingActivityChange} />
                        </div>
                    </div>
                    <div className={style.activityTrack}>
                        <div className={style.penalties}>
                            <label htmlFor="penaltiesApply">Штрафи застосовуються?</label>
                            <input id={"penaltiesApply"} type={"checkbox"} checked={this.state.arePenaltiesApply} onChange={this.togglePenaltiesApply} />
                        </div>
                    </div>
                    
                </div>
                <div className={style.borderBottom}></div>
            </div>
        );
    }
}

export default Report;