export const SECONDS_IN_DAY = 86400;

export const isDateInSameDay = (timestamp: number): boolean => {
    const diff = Math.floor(Date.now() / 1000) - timestamp;
    return diff / SECONDS_IN_DAY < 3;
}

export const getLocaleDateString = (timestamp: number): string => {
    return new Date(timestamp * 1000).toLocaleDateString();
}

export const getTodaysLocaleDateString = (): string => {
    return new Date(Date.now()).toLocaleDateString();
}

export const getTodaysTimestamp = (): number => {
    const now = Math.floor(Date.now() / 1000);
    return now - (now % SECONDS_IN_DAY);
}

export const getDayOfWeek = (timestamp: number): number => {
    return new Date(timestamp * 1000).getDay();
}

export const daysAreMisted = (timestamp: number): number => {
    return (getTodaysTimestamp() - timestamp) / SECONDS_IN_DAY - 1;
}

export const getYearAndMonth = (timestamp: number): string => {
    const months = ["Січень", "Лютий", "Березень", "Квітень", "Травень", "Червень", 
                    "Липень", "Серпень", "Вересень", "Жовтень", "Листопад", "Грудень"];

    const date = new Date(timestamp * 1000);
    return `${months[date.getMonth()]}, ${date.getFullYear()}`;
}