import { Dispatch } from "@reduxjs/toolkit";
import { AppActions, AppThunk } from "../redusers/redux-store";
import { User } from "../../components/auth/User";
import { LOGIN_ACTION, LOGOUT_ACTION } from "../redusers/auth-redusers";
import { authService } from "../../services/auth-service";

const actions = {
    login: (username: string, role: string) => ({type: LOGIN_ACTION, payload: {username, role}} as const),
    logout: () => ({type: LOGOUT_ACTION} as const),
}

export const login = (login: string, password: string): Thunk => {
    return async (dispatch: Dispatch) => {
        const user = authService.getUser(login, password);
        if (user) {
            dispatch(actions.login(user.login, user.role));
        }
    }
}

export const logout = (): Thunk => {
    return async (dispatch: Dispatch) => {
        authService.logout();
        dispatch(actions.logout());
    }
}

export type AuthActions = AppActions<typeof actions>
type Thunk = AppThunk<AuthActions>