import { User } from "../components/auth/User";

const users = [
    {
        login: "perijok",
        password: "perijok228",
        role: "user"
    },
    {
        login: "odmen229",
        password: "odmen229",
        role: "admin"
    }
] as User[];

export const authService = {
    getUser(login: string, password: string): User | undefined {
        const user = users.find(p => p.login === login && p.password === password);
        if (user){
            localStorage.setItem("username", user.login);
            localStorage.setItem("role", user.role);
        }

        return user;
    },

    logout(): void {        
        localStorage.clear();
    },

    isUserNotAuthorized(): boolean {
        const userName = localStorage.getItem("username");
        return !userName;
    },

    isUserAuthorized(): boolean {
        const userName = localStorage.getItem("username");
        return !!userName;
    },

    isUserAdmin(): boolean {
        const role = localStorage.getItem("role");
        return role === "admin";
    }
}