import { AuthActions } from "../actions/auth-actions";

export const LOGIN_ACTION = "AUTH/LOGIN";
export const LOGOUT_ACTION = "AUTH/LOGOUT";

let initialState = {
    username: localStorage.getItem("username"),
    role: localStorage.getItem("role")
};

const authRedusers = (state = initialState, action: AuthActions): typeof initialState => {
    switch (action.type) {
        case LOGIN_ACTION:
            return {...state, username: action.payload.username, role: action.payload.role};

        case LOGOUT_ACTION:
            return {...state, username: null, role: null};

        default:
            return state;
    }
}

export default authRedusers;