import { ReportItem } from "../components/report/ReportItem";
import { getDayOfWeek } from "./time-utils";

export const calculateMoney = (item: ReportItem): number => {
    const currentDay = getDayOfWeek(item.date);
    const isWeekend = currentDay === 6 || currentDay === 0;
    const isWakeUpInTime = item.wakeUp <= 9 * 60;
    const isGoToSleepInTime = (item.goToSleep >= 19 * 60 && item.goToSleep < 24 * 60) || item.goToSleep <= 1 * 60;

    let money = 0;
    money += Math.min(6, item.learning) * 10;
    money += Math.min(2, item.sports) * 60;
    money += Math.min(5, item.cooking) * 10;
    money += item.distance >= 4 ? 20 : 0;
    money += isWakeUpInTime ? 30 : 0;
    money += isGoToSleepInTime ? 30 : 0;

    if (!isWeekend && item.arePenaltiesApply) {
        money -= item.sports < 1 ? 30 : 0;
        money -= item.learning < 2 ? 30 : 0;

        if (!isWakeUpInTime) {
            money -= Math.floor((item.wakeUp - 9 * 60) / 30) * 5;
        }

        if (!isGoToSleepInTime) {
            money -= Math.floor((item.goToSleep - 1 * 60) / 30) * 5;
        }
    }

    return money;
}

export const getDefaultItem = (date: number): ReportItem => {
    let item = {
        id: "0",
        distance: 0,
        sports: 0,
        learning: 0,
        cooking: 0,
        wakeUp: 9*60,
        goToSleep: 0,
        money: 0,
        date: date,
        arePenaltiesApply: true
    } as ReportItem;

    item.money = calculateMoney(item);
    return item;
}