import { connect } from "react-redux";
import Stats from "./Stats";
import { AppStore } from "../../store/redusers/redux-store";
import { getReports } from "../../store/actions/report-actions";

const mapState = (store: AppStore) => {
    return ({
        items: store.reportsData.reports,
        adminMode: false
    });
}

export default connect(mapState, {getReports})(Stats);